

























































































































import { Vue, Component, Watch } from "vue-property-decorator";
import * as _ from "lodash";
import { supplyApi } from "@oa/api";
@Component({})
export default class PushInPage extends Vue {
	orderState: string = "";
	supplyList: any = [];
	SceneList2: any = [
		{
			name: "工程管理类",
			children: [
				{
					name: "污水处理及配套设施",
					val: `11`,
					children: [
						{
							name: "1类项目",
							val: `962`,
						},
						{
							name: "2类项目",
							val: `4186`,
						},
					],
				},

				{
					name: "饮用水源地污染防治项目",
					val: `12`,
					children: [
						{
							name: "地表水治理",
							val: `830`,
						},
						{
							name: "山泉水饮用",
							val: `213`,
							children: [
								{
									name: "开发",
									val: `213-1`,
								},
								{
									name: "环境维护",
									val: `213-2`,
								},
							],
						},
					],
				},
				{
					name: "区域水环境综合整治",
					val: `13`,
				},
			],
		},
		{
			name: "管理类项目",
			children: [
				{
					name: "土地管理",
					val: `21`,
					children: [
						{
							name: "土地面测绘",
							val: `21-1`,
						},
						{
							name: "土地资源整合",
							val: `21-2`,
						},
					],
				},
				{
					name: "环境管理",
					val: `22`,
				},
				{
					name: "电力管理",
					val: `23`,
				},
			],
		},
	];
	//  isOk: boolean = true;
	model: any = {
		flag: 1,
		amount: "",
		unit: "",
		supplies: "",
		operatorName: "",
		operateTime: "",
		specification: "",
		relateUrl: "",
	};

	//  debounceInput = this.debounceHandler(this.watchNumber,1000)
	//  debounceHandler(func:any,duration:any){
	//      return _.debounce(func,duration,true);
	//  }
	get isOk() {
		if (
			this.model.supplies &&
			this.model.operatorName &&
			this.model.relateUrl &&
			this.model.operateTime &&
			this.model.amount
		) {
			if (this.watchNumber()) {
				return true;
			}
		} else {
			return false;
		}
	}

	// @Watch('model.amount')
	watchNumber() {
		const reg = /^\d{1,}$/;
		const result = reg.test(this.model.amount);
		if (!result) {
			return false;
		}
		return true;
	}

	async getSupplyName() {
		const params = {
			name: this.model.name,
		};
		try {
			const res = await supplyApi.getSupplyName(params);
			this.supplyList = res.data;
		} catch (error) {
			console.log(error);
		}
	}

	async submit() {
		this.model.operateTime = this.$utils.dateFormat(
			this.model.operateTime,
			"yyyy-MM-dd HH:mm:ss"
		);
		const params = this.model;
		try {
            const res = await supplyApi.suppliesOut(params);
			this.$message({
                // message: res.message=='success' && "出库成功!",
                message: res.message=='success'?'出库成功!':res.message,
                type:'success'
			});
		} catch (error) {
			this.$message({
				message: error.data.message,
			});
		}
	}

	setAmount() {
		this.$set(this.model, "amount", "");
	}

	orderStateList: any = [
		{
			id: "0",
			name: "待审核",
		},
		{
			id: "1",
			name: "待接单",
		},
	];

	mounted() {
		this.getSupplyName();
	}
}
